.party-config__layout {
  display: flex; }
  .party-config__layout__left {
    padding-right: 25px;
    flex: 0 0 auto;
    width: calc(100% - 245px); }
  .party-config__layout__right {
    flex: 0 0 auto;
    width: 245px; }

.party-config__promo {
  padding: 100px 55px;
  margin: 0 1px;
  background: url(../images/promo.jpeg) no-repeat 50% 50%;
  background-size: cover; }
  .party-config__promo__text {
    padding: 40px;
    max-width: 575px;
    background: rgba(196, 72, 144, 0.9);
    color: #fff; }
  .party-config__promo__title {
    font-family: "BebasNeue";
    font-size: 50px;
    font-weight: 700;
    line-height: 1; }
  .party-config__promo__desc {
    font-size: 18px;
    line-height: 1.55556; }

.party-config__group {
  margin: 30px 0;
  padding: 30px 40px;
  border: 1px solid #d1d1d1;
  border-radius: 2px;
  box-shadow: 0px 5px 9.2px 0.8px rgba(0, 0, 0, 0.1);
  background: #fff; }
  .party-config__group--confetti {
    background: #fff url(../images/confetti.jpeg) no-repeat 50% 100%;
    margin-bottom: 0; }

.party-config__about {
  margin-top: 0;
  text-align: center; }
  .party-config__about__text {
    max-width: 1000px;
    margin: 0 auto; }
  .party-config__about__title {
    font-family: "BebasNeue";
    font-size: 50px;
    font-weight: 700;
    line-height: 1;
    color: #c44890; }
  .party-config__about__desc {
    margin-top: 20px;
    font-size: 16px;
    line-height: 1.5; }
  .party-config__about__btn {
    margin-top: 25px; }

.party-config__steps {
  display: flex;
  margin: 20px -10px 35px -10px;
  font-family: "PT Serif Caption"; }
  .party-config__steps__col {
    flex: 0 0 auto;
    width: 20%;
    padding: 0 10px;
    display: flex;
    flex-direction: column; }
  .party-config__steps__step {
    margin: 0 auto 17px auto;
    width: 2.4em;
    height: 2.4em;
    border-radius: 50%;
    border: 1px dashed #555451;
    font-size: 25px;
    line-height: 2.32; }
  .party-config__steps__desc {
    flex-grow: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.5; }

.party-config__attn {
  margin: 10px 0 20px 0;
  padding: 13px 0;
  background: url(../images/pink-bubbles.png);
  font-family: "SummerFont";
  font-size: 48px;
  text-transform: uppercase;
  text-align: center;
  color: #fff; }

.party-config__sep {
  border-top: 1px dashed #76665c;
  margin: 10px 0 20px 0; }
